.main__contact {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba($color: #000000, $alpha: 0.6);
    opacity: 1;
    transition: .9s;
    z-index: 97;
}

.contact__wrap {
    position: absolute;
    right: 0px;
    bottom: 0;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    background-color: #000000;
    width: 100%;
    height: 85%;
    z-index: 96;
    box-sizing: border-box;
}

.hide{
    opacity: 0;
    width: 0;
    
    transition: .9s all;
}

.contact__form {
    margin-right: 30px;
}

.contact__map {
    display: flex;
    align-items: center;
}

.contact__list {
    list-style: none;
    padding: 0;
    margin-top: 60px;
    margin-left: 60px;
}

.contact__text {
    color: #0099cc;
    font-weight: 700;
    font-size: 30px;
}

.contact__info {
    width: 204px;
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    font-family: 'MyriadPro';
}

.mail__form {
    margin-top: 60px;
}

.mail__form legend{
    margin-bottom: 30px;
    color: #0099cc;
    font-weight: 700;
    font-size: 30px;
    width: 237px;
}

.mail__form input {
    width: 236px;
    height: 45px;
    box-shadow: inset 8px 13px 5px rgba(0, 0, 0, 0.25);
    background-color: #ffffff;
    border: none;
    border-radius: 5px;
    margin-bottom: 12px;
    font-family: 'MyriadPro';
    padding-left: 20px;
    box-sizing: border-box;
}

.form__text {
    width: 236px;
    height: 100px;
    box-shadow: inset 8px 13px 5px rgba(0, 0, 0, 0.25);
    background-color: #ffffff;
    border: none;
    border-radius: 5px;
    margin-bottom: 12px;
    font-family: 'MyriadPro';
    padding-left: 20px;
    box-sizing: border-box;
    padding-top: 20px;
}

.btn-primary {
    background-color: #63ccff;
    height: 45px;
    width: 236px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    text-transform: uppercase;
    color: #ffffff;
    font-family: 'MyriadPro';
}

//-----------publich-----------

.publich__text, .publich__info {
    margin-left: 20px;
    width: 90%;
    font-family: Roboto;
}

.publich__list {
    width: 285px;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-left: 60px;
    margin-top: 30px;
}

.publich__item {
    margin-bottom: 10px;
    display: flex;
    transition: .5s;
    position: relative;
}

.publich__item:hover {
    border-bottom: 1px solid #0099cc;
}

.publich__item:hover, .publich__item:hover a{
    color: #0099cc;
    transition: .5s;
    
}

.publich__item svg {
    width: 13px;
    margin-right: 5px;
}

.publish__link {
    text-decoration: none;
    color: #ffffff;
    font-weight: 400;
    transition: .5s;
}

.publish__pdf {
    position: absolute;
    right: 0;
}

// ----------------prise ------------

.prise {
    width: 80%;
    font-family: "Myriad Pro";
    font-size: 13px;
    line-height: 18px;
}

.prise span {
    font-size: 16px;
}

.mail__price {
    margin-left: 60px;
}

@media (min-width: 1190px) {
    .contact__wrap {
        width: 1151px;
        height: 683px;
        top: 150px;
    }

    .contact__map {
        display: flex;
    }

    .publich__info {
        width: 460px;
    }

    .prise {
        width: 671px;
    }
}
