@import "fonts.scss";

body {
  min-width: 320px;
  height: 100vh;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: "Roboto";
  font-size: 14px;
  color: #ffffff;
  background-image: url("../img/Background_2.png");
  background-repeat: no-repeat;
  background-size: auto;
}


.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

header {
  
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header__wrap {
  position: relative;
  height: 130px;
}

.header__nav {
  position: relative;
}

//----------Логотип------------

.logo__img {
  margin-top: 20px;
  margin-left: 20px;
}

.header__phone {
  color: #ffffff;
  font-size: 24px;
  font-weight: 500;
  margin: 0;
  margin-top: 10px;
  margin-left: 20px;
  line-height: 0;
  position: relative;
  z-index: 96;
}

.menu__list {
  visibility: hidden;
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: -300px;
  right: 0;
  height: 300px;
  width: 100%;
  background: rgba(0,0,0,0.6);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: .5s all;
  z-index: 98;
}

.menu__item {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.menu__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-weight: 300;
  transition: .5s;
}

.menu__link:hover {
  color: #6aceff;
  transition: .5s;
}


//-----------Навигация--------------
.burger__list {
  position: absolute;
  display: block;
  top: -40px;
  right: 30px;
  list-style: none;
  width: 16px;
  height: 14px;
  cursor: pointer;
  padding: 0;
  margin: 0;
  z-index: 99;
}

.burger__item {
  height: 2px;
  width: 16px;
  background: #ffffff;
  position: absolute;
  transition: .7s all;
}
.second, .third {
  top: 5px;
}

.fourth {
  top: 10px;
}

#check-menu {
  visibility: hidden;
}

#check-menu:checked ~ .menu__list{
  visibility: unset;
  top: -65px;
}

.second--active {
  transform: rotate(45deg);
  background: #ffffff;
}

.third--active {
  transform: rotate(-45deg);
  background: #ffffff;
}

// ------- Main ------------

.main__info, .main__text, .main__word {
  margin-left: 20px;
  width: 80%;
  margin-top: 30px;
  margin-bottom: 30px;
}

.main__social {
  display: none;
}

//--------footer-------------

.footer__wrap {
  position: absolute;
  bottom: 0;
}

.footer__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  padding: 0;
  
}

.footer__item {
  display: flex;
  flex-direction: column;
  text-align: center;
  cursor: pointer;
}

.footer__item a:hover {
  transition: .5s;
  color: #6aceff;
}

.footer__item a {
  color: #ffffff;
  text-decoration: none;
  transition: .5s;
}

.footer__img {
  fill: #ffffff;
  width: 40px;
  height: 49px;
  transition: .5s;
}

.footer__item:hover .footer__img {
  fill: #6aceff;
  transition: .5s;
}

.footer__info {
  width: 138px;
}




@media (min-width: 768px){

  .menu__item {
    align-items: unset;
    
  }

  .menu__item a {
    width: 140px;
    font-size: 14px;
  }

  .main__page {
    height: 80%;
  }

  .header__phone {
    position: absolute;
    right: 30px;
  }
  
  .footer {
    position: fixed;
    bottom: 0;
    width: 100%;
  }

  .footer__wrap {
    width: 100%;
  }

  .footer__list {
    justify-content: flex-end;
    margin-right: 30px;
  }

  .footer__item:nth-child(1) {
    margin-right: 70px;
  }

  .footer__item:nth-child(2) {
    margin-right: 70px;
  }

  .main__social {
    visibility: hidden;
  }

  .main__info {
    margin-top: 0;
    font-weight: 300;
    font-size: 24px;
  }

  .main__text {
    font-size: 60px;
    width: 597px;
  }

  .main__word {
    width: 486px;
    font-size: 24px;
    font-weight: 300;
  }
  
}

@media (min-width: 800px){

  .burger__list {
    visibility: hidden;
  }

  .menu__list {
    height: 30px;
    top: -36px;
    right: 15px;
    background: none;
    flex-direction: row;
    justify-content: flex-end;
    visibility: unset;
    z-index: 98;
  }

  .menu__item {
    width: 95px;
  }

  .menu__item:nth-child(4) {
    width: 140px;
  }

  .header__phone {
    right: 30px;
  }
}

@media (min-width:1200px) {
  body {
    background-size: cover;
  }

  .header {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .header__wrap {
    width: 1148px;
  }

  .header__phone {
    right: 15px;
  }

  .main__page {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .main__wrap {
    width: 1148px;
  }

  .footer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .footer__wrap {
    width: 1148px;
  }

  .footer__list {
    margin-right: 0;
  }

  .main__info {
    margin-top: 0;
    font-weight: 300;
    font-size: 24px;
  }

  .main__text {
    font-size: 60px;
    width: 597px;
  }

  .main__word {
    width: 486px;
    font-size: 24px;
    font-weight: 300;
  }

  .main__block {
    width: 1148px;
    display: flex;
    flex-direction: row;
  }

  .main__wrap {
    display: flex;
    flex-direction: column;
  }

  .main__social {
    visibility: unset;
    display: block;
    width: 100px;
    display: flex;
    align-items: center;
  }

  .menu__list {
    height: 30px;
    top: -36px;
    right: 0;
    background: none;
    flex-direction: row;
    justify-content: flex-end;
    visibility: unset;
    z-index: 98;
  }

  .burger__list {
    visibility: hidden;
  }

  .main__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  

  .main__item:nth-child(1){
    width: 2px;
    height: 262px;
    background: #ffffff;
    display: flex;
    align-items: center;
  }

  .main__item:nth-child(2), .main__item:nth-child(3), .main__item:nth-child(4){
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .main__item:nth-child(5){
    width: 2px;
    height: 262px;
    background: #ffffff;
    display: flex;
    align-items: center;
  }
}


